@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  --color-one :#4f338f;
  --color-two :#aa7dff;
  --color-three :#e5d8ff;
  --color-four :#efebff;
  margin: 0px;
  font-family:'Dosis', sans-serif;
  color: var(--color-four);
}
nav{
    /* margin: 0; */
    width: auto;
    height: 4rem;
    background-color: var(--color-two);
    display: grid;
    grid-template-rows: 5% 80% 5%;
    grid-template-columns: 5% 10% 70% 10% 5%;
}
.nav-logo{
    grid-column: 2/3;
    grid-row: 2/3;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;

    width: 1.5em;
    height: 1.5em;
    background-color: var(--color-one);
    border-radius: 30px;

    
}
.nav-hamburguerMenu{
    height: 100%;
    grid-column: 4/5;
    grid-row: 2/3;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--color-one);
    border-radius: 5px;
    font-size: 1.3rem;
}

.presentation{
    background-image: url(/portfolio/static/media/freelancer.1dc1e34f.svg);
    height: 35em;
    background-position: bottom;
    background-repeat: no-repeat;
    background-size: 30em;
    position: relative;
    display: grid;
    grid-template-rows: 10% 10% 10% 10% 10% 10% 10% 10% 10% 10% ;
    grid-template-columns: 10% 20% 20%  20% 20% 10%;
}

.presentation-helloWorld{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 3rem;
    grid-row: 2/4 ;
    grid-column: 3/5;
    color: var(--color-one);
}
.presentation-about{
    font-size: 1.5rem;
    color: var(--color-one);
    grid-row: 4/5 ;
    grid-column:2/6;
    display: flex;
    /* border: solid 1px red; */
    justify-content: center;
    align-items: center;
}

.presentation-contactMe{
    grid-row: 9/11 ;
    grid-column: 4/5 ;
    background-color: var(--color-three);
    border-radius: 5px;   
    display: flex;
    justify-content: center;
    align-items: center ;
    flex-direction: column;
    -webkit-transition: .5s;
    transition: .5s;
}
.presentation-contactMe:hover{
    border: 4px solid var(--color-two);
}
.presentation-contactMe p {
    color: var(--color-one);
    font-size: 1.5em;
}

.presentation-contactMe_gitTwitLinkendin{
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.presentation-contactMe a img{
    width: 5em;
    /* height: 3em; */
}
.presentation-contactMe a img{
    display: flex;
    justify-content: center;
    align-items: column;
}
.presentation-contactMe_github{
    width: 3em !important;
}
.card{
    border-radius: 5px;
    background-color: var(--color-one);
    grid-column: 1/6;
    height:17em ;
    padding: .5em;
    display: grid;
    grid-template-columns: 39% 1%  20% 20% 20%  ;
    margin-bottom: 2em;
}
.card-left{
    
    grid-column:1/2;
    display: flex;
    justify-content: center;
    align-items: center;
    
}

.card-left_img{
    width: 100%;
}

.card-right{
    padding: .5em 0em;
    grid-column: 3/6 ;    
    display: grid;
    grid-template-rows: 5% 15% 60% 15% 5%;
    grid-template-columns: repeat(10, 10%) ;
}

.card-right > h2 {
    grid-row: 2/3;
    grid-column: 1/7;
}
.card-right > p {
    margin-top: .5em;
    grid-row: 3/4;
    grid-column: 1/11;
    
}

.card-right_appLink{
    margin: .25em;
    background-color: var(--color-four);
    border-radius: 10px;
    box-shadow: 0px 0px 15px var(--color-two);

    color: var(--color-one);
    font-size: 1.5rem;
    
    grid-row: 2/3;
    grid-column: 7/10;
    
    display: flex;
    justify-content: center;
    align-items: center;
}

.card-right_tecnologies{
    grid-row: 4/5;
    grid-column: 1/11;
    display: flex;
    align-items: center;

}

.card-right_tecnologies > p{
    margin-right: .5em;        
}
.card-right_tecnologies > img{
    width: 2em;
    height: 2em;       
}

.proyects{
    background-color: var(--color-four);
    padding: 3em 5em;
    display: grid;
    grid-template-columns: repeat(5, 20%);
}
